import React from "react";
import { Form, Input, Icon, Row, Col, Button, Divider, Modal } from "antd";
import "./Signup.less";
import axios from "axios";
import { preURL } from "../../axios/config";
import { hashHistory } from "react-router";
import { instanceOf } from "prop-types";
import { withCookies, Cookies } from "react-cookie";

const FormItem = Form.Item;

class Signup extends React.Component {
  static propTypes = {
    cookies: instanceOf(Cookies).isRequired
  };
  state = {
    confirmDirty: false
  };

  componentWillMount() {
    const { cookies } = this.props;

    if (cookies.get("user")) {
      hashHistory.push("/app/recite");
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        axios({
          method: "post",
          url: preURL + "/api/signup",
          dataType: "json",
          data: {
            email: values.email,
            password: values.password,
            name: values.name
          },
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          }
        })
          .then(response => {
            console.log("register response:", response);
            if (response.data.code === "200") {
              const { router } = this.props;
              const modal = Modal.success({
                title: "",
                content: "3s"
              });
              setTimeout(() => {
                modal.destroy();
                router.push("/userservice/login");
              }, 3000);
            } else if (response.data.code === "1037") {
              Modal.error({
                title: "",
                content: ""
              });
            } else {
              Modal.error({ title: "", content: "..." });
            }
          })
          .catch(error => {
            console.log("register error:", error);
          });
      }
    });
  };

  handleConfirmBlur = e => {
    const value = e.target.value;
    this.setState({
      confirmDirty: this.state.confirmDirty || !!value
    });
  };

  checkConfirm = (rule, value, callback) => {
    const form = this.props.form;
    if (value && this.state.confirmDirty) {
      form.validateFields(["confirm"], { force: true });
    }
    callback();
  };

  checkPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value !== form.getFieldValue("password")) {
      callback("Two passwords that you enter is inconsistent!");
    } else {
      callback();
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: {
          span: 4
        },
        sm: {
          span: 4
        }
      },
      wrapperCol: {
        xs: {
          span: 20
        },
        sm: {
          span: 20
        }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 16,
          offset: 8
        }
      }
    };

    return (
      <div className="signup_outer">
        <div className="signup_all_container">
          <h1>
            <strong></strong>
          </h1>
          <Button
            onClick={() => {
              hashHistory.push("/userservice/login");
            }}
            className="goback_login"
          >
            <Icon type="arrow-left" />
          </Button>
          <Divider />
          <div className="signup_container">
            <Form onSubmit={this.handleSubmit}>
              <FormItem {...formItemLayout} label={<span>  </span>}>
                <Row gutter={8}>
                  <Col span={16}>
                    {getFieldDecorator("email", {
                      rules: [
                        {
                          type: "email",
                          message: ""
                        },
                        {
                          required: true,
                          message: "!",
                          whitespace: true
                        }
                      ]
                    })(
                      <Input
                        onChange={e => {
                          this.setState({ userMail: e.target.value });
                        }}
                      />
                    )}
                  </Col>
                </Row>
              </FormItem>
              <FormItem {...formItemLayout} label="">
                <Row gutter={8}>
                  <Col span={16}>
                    {getFieldDecorator("password", {
                      rules: [
                        {
                          required: true,
                          message: "!"
                        },
                        {
                          validator: this.checkConfirm
                        },
                        {
                          min: 6,
                          message: "6"
                        },
                        {
                          max: 20,
                          message: ""
                        }
                      ]
                    })(
                      <Input type="password" placeholder="620" />
                    )}
                  </Col>
                </Row>
              </FormItem>
              <FormItem {...formItemLayout} label="">
                <Row gutter={8}>
                  <Col span={16}>
                    {getFieldDecorator("confirm", {
                      rules: [
                        {
                          required: true,
                          message: "!"
                        },
                        {
                          validator: this.checkPassword,
                          message: ""
                        }
                      ]
                    })(
                      <Input type="password" onBlur={this.handleConfirmBlur} />
                    )}
                  </Col>
                </Row>
              </FormItem>
              <FormItem {...formItemLayout} label={<span>  </span>}>
                <Row gutter={8}>
                  <Col span={16}>
                    {getFieldDecorator("name", {
                      rules: [
                        {
                          required: true,
                          message: "(20)",
                          whitespace: true
                        },
                        {
                          max: 20,
                          message: ""
                        }
                      ]
                    })(<Input />)}
                  </Col>
                </Row>
              </FormItem>
              <FormItem {...tailFormItemLayout}>
                <Button type="primary" htmlType="submit">
                  
                </Button>
              </FormItem>
            </Form>
          </div>
        </div>
      </div>
    );
  }
}

const WrappedRegistrationForm = Form.create()(Signup);

export default withCookies(WrappedRegistrationForm);



